* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.order_page {
  width: 100%;
  height: auto;
  display: flex;
  margin: 80px 0;
}

.order_page .order_left {
  width: 70%;
  height: auto;
}

.order_page .order_right {
  /* margin-top: 86px; */
  margin-left: 30px;
  width: 30%;
  height: auto;
  display: flex;
  flex-direction: column;
}

.order_page .order_left .order_nav_frame {
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: center;
}
.order-frame_box {
  width: 75%;
}
/* .css-1gsv261 {
  border: none !important;
} */

.order_page .order_right .order_info_box {
  padding: 20px;
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: inset 0px 1px 1px rgb(0 0 0 / 25%),
    inset 0px 1px 8px rgb(0 0 0 / 25%), inset 0px 4px 16px rgb(242 189 87 / 4%);
  border-radius: 16px;
}

.order_page .order_info_box .order_user_img {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.order_page .order_info_box .order_user_img .order_user_avatar {
  width: 117px;
  height: 112px;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  border-radius: 37px;
}

.order_page .order_info_box .order_user_icon_box {
  position: absolute;
  padding: 5px;
  width: 30px;
  height: 30px;
  top: 81%;
  right: 0;
  display: flex;
  background: rgba(0, 0, 0, 0.33);
  border-radius: 37px;
}

.order_page .order_user_icon_box .order_user_prof_img {
  filter: brightness(0) invert(1);
}

.order_page .order_info_box .order_user_name {
  margin-top: 10px;
  font-size: 28px;
  line-height: 36px;
  color: #08090d;
  text-transform: capitalize;
}

.order_page .order_info_box .order_user_prof {
  font-size: 20px;
  line-height: 36px;
  color: #a1a1a1;
}

.order_page .order_info_box .order_user_address {
  margin-top: 10px;
  width: 90%;
  display: flex;
  flex-direction: row;
}

.order_page .order_info_box .card_input {
  margin-top: 10px;
  padding: 10px;
  width: 100%;
  height: 40px;
  background: #f5f5f5;
  border-radius: 10px;
  font-family: "Nunito";
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  border: 0.5px solid #b3a6a6;
  letter-spacing: -0.02em;
  color: #434141;
}

.order_page .order_info_box .card_half_input {
  margin-top: 10px;
  padding: 10px;
  width: 49%;
  height: 40px;
  background: #f5f5f5;
  border-radius: 10px;
  font-family: "Nunito";
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  border: 0.5px solid #b3a6a6;
  letter-spacing: -0.02em;
  color: #434141;
}

.order_page .order_info_box .card_input {
  margin-top: 10px;
  padding: 10px;
  width: 100%;
  height: 40px;
  background: #f5f5f5;
  border-radius: 10px;
  font-family: "Nunito";
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  border: 0.5px solid #b3a6a6;
  letter-spacing: -0.02em;
  color: #434141;
}

.order_page .order_info_box .cards_box {
  margin-top: 35px;
  width: 80%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.order_page .order_info_box .cards_box img {
  width: 39px;
  height: 26px;
}

.order_page .order_info_box .spec_address_txt {
  margin-left: 5px;
  font-family: "DM Sans";
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #616164;
}

.order_page .order_main_content {
  width: 100%;
  height: auto;
}

.order_page .order_main_content .order_main_box {
  position: relative;
  width: 100%;
  max-height: 290px;
  margin-bottom: 25px;
  background: #f4f4f4;
  border: 1px solid #c8c7c7;
  border-radius: 16px;
}

.order_page .order_main_content .order_main_box .order_box_scroll {
  position: relative;
  overflow: hidden;
  width: 100%;
  max-height: 195px;
  margin-top: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: auto;
}

.order_page .order_box_scroll .ordersName_price {
  position: relative;
  width: 95%;
  height: 47px;
  margin-top: 5px;
  margin-left: 40px;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.order_page .order_box_scroll .ordersName_price .orderDishImg {
  position: relative;
  width: 50px;
  height: 47px;

  border-radius: 29px;
  background-size: cover;
}

.order_page .order_box_scroll .ordersName_price .titleDish {
  position: relative;
  width: 260px;
  height: 36px;
  margin-left: 20px;
  font-size: 16px;
  line-height: 36px;
  color: #000000;
}

.order_page .order_box_scroll .ordersName_price .priceBox {
  position: relative;
  max-width: 170px;
  height: 100%;
  margin-left: 70px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.order_page .order_box_scroll .ordersName_price .priceBox p {
  font-weight: 400;
  font-size: 18px;
  line-height: 27px;
  font-size: 16px;
  color: #262626;
}

.order_main_box .total_price_box {
  position: relative;
  width: 96%;
  margin: 0 auto 10px auto;
  height: 45px;
  margin-top: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  background: #c5c5c5;
}

.red_solid {
  background: #0288d1 !important;
}
.red_solid p {
  color: #fff !important;
}

.red_solid .price_total_icon {
  color: #fff;
  font-size: 22px;
}
.order_page .order_main_content .order_main_box .total_price_box .boxTotal {
  position: relative;
  max-width: 535px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 25px;
}

.order_page .total_price_box .boxTotal p {
  margin-left: 20px;
  font-size: 18px;
  line-height: 36px;
  color: #000000;
}

.order_page .total_price_box .boxTotal .data_compl {
  font-size: 14px;
  line-height: 36px;
  color: #000000;
}

.order_empty {
  text-align: center;
  margin-top: 90px;
  font-size: 19px;
  color: #000000;
}
